/**
 * Rows & Columns
 */

.row {
	display: flex;
	flex-wrap: wrap;
	--pg0: var(--grid-gap-0);
	--pg1: var(--grid-gap-1);
	--pg2: var(--grid-gap-2);
	--pg3: var(--grid-gap-3);
	--pg4: var(--grid-gap-4);
	--pg5: var(--grid-gap-5);
	--ng0: calc(var(--grid-gap-0) * -1);
	--ng1: calc(var(--grid-gap-1) * -1);
	--ng2: calc(var(--grid-gap-2) * -1);
	--ng3: calc(var(--grid-gap-3) * -1);
	--ng4: calc(var(--grid-gap-4) * -1);
	--ng5: calc(var(--grid-gap-5) * -1);
}

.row--hcenter {
	justify-content: center;
}

.row--vcenter {
	align-items: center;
}

